@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600;700&display=swap);
*{box-sizing:border-box}body{background-color:#f3fbfb;font-family:"Open Sans", sans-serif}.app{display:flex;flex-direction:column;align-items:center}

.comment{display:flex;flex-direction:column;grid-gap:10px;gap:10px;box-shadow:0 0 10px rgba(197,197,197,0.5);border-radius:20px;padding:10px;font-size:13px}.comment__autor{display:flex;align-items:center}.comment__img{width:40px;height:40px;border-radius:50%;margin-right:10px;object-fit:cover}.comment__email{color:#888785}.comment__name{margin:0}.comment__body{margin:0 0 20px}

.commentList{display:flex;flex-direction:column;grid-gap:20px;gap:20px}.commentList__title{border-top:1px solid #eee;margin:10px 0 0;padding-top:10px}

.userInfo{display:flex;flex-direction:row-reverse;align-items:center;align-self:flex-end;grid-gap:10px;gap:10px}.userInfo__img{width:40px;height:40px;border-radius:50%;margin-right:10px;object-fit:cover}.userInfo__email{color:#888785}

.postInfo__title{margin:10px 0;text-transform:uppercase}.postInfo__body{font-size:14px;margin:0 0 30px;font-weight:500;color:#464444}

.post{background-color:#fff;border-radius:10px;box-shadow:0 2px 20px rgba(0,0,0,0.1);overflow:hidden;width:600px;margin-bottom:30px}.post__img{width:100%;height:200px;object-fit:cover}.post__body{display:flex;flex-direction:column;align-items:flex-start;padding:20px;min-height:250px}

